/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
*{
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

.width250{
  width: 250px;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

/*
font-family: 'Anton', sans-serif;
font-family: 'Montserrat', sans-serif;
font-family: 'Oswald', sans-serif; 
*/
body {
  font-family: 'Montserrat', sans-serif;
  color: #333;
}

.padding-top60{
  padding-top: 60px;
}
.container{
  width: 720px;
  margin: 0 auto;
  display: flex;
}
.float-right{
  float:right;
} 
.text-bold{
  font-weight: bold;
}
.text-align-right{
  text-align: right;
}
.text-transform-uppercase{
  text-transform: uppercase;
}

.top-header{
  flex-basis: 250px;
}
.header-list{
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: flex-end;
  padding-right: 40px;
}
.header-list-item{
  font-family: 'Oswald', sans-serif;
  font-size: 26px;
  font-weight: bold;
  text-transform: uppercase;
  border-bottom: 2px solid #333;
  margin-top: 20px;
  text-align: right;
  width: 100%;
}
.header-list-item a {
  text-decoration: none;
  color: #333;
  padding-bottom: 5px;
  display: inline-block;
  position: relative;
}
a.is-active-link:after{
  content: '';
  border: solid #333;
  position: absolute;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  top: 10px;
  right: -20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.display-area{
  /* background: #f9f9f9; */
  width: 460px;
  padding: 40px;
  flex: 1 auto;
}

.profile-section{
  width: 460px;
}
.profile-image{
  width: 300px;
  filter: grayscale(0.50);
  border: 1px solid #ababab;
}
.profile-section .image-section{
  position: relative;
  display: block;
  margin-bottom: 80px;
}
.profile-section .image-section:after{
  content: '';
  display: block;
  width: 300px;
  height: 100%;
  border: 1px solid #333;
  position: absolute;
  top: 40px;
  left: 40px;
  z-index: -1;
}

.page-title{
  font-family: 'Anton', sans-serif;
  text-transform: lowercase;
  color: #ccc;
  font-size: 32px;
}
.page-subtitle {
  font-size: 32px;  
  font-weight: bold;
  letter-spacing: -1px;
  margin-bottom: 20px;
  font-family: 'Oswald', sans-serif;
}

.one-liner {
  line-height: 20px;
}

/* Experience page */
.slider-arrow{
  border: 3px solid #555;
  border-top: none;
  border-left: none;
  padding: 10px;
  cursor: pointer;
}
.right-arrow{
  transform: rotate(-45deg);
  position: absolute;
  right: 5px;
  top: 5px;
}
.left-arrow{
  transform: rotate(135deg);
  position: absolute;
  right: 40px;
  top: 5px;
}
.experience-section{
  width: 390px;
  overflow: hidden;
  padding-top: 50px;
  position: relative;
}
.exp-list{
  display: flex;
}
.exp-list-item{
  min-width: 390px;
}
.exp-top-section{
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.exp-top-blocks{}
.block-title{
  font-family: 'Oswald', sans-serif;
  font-size: 18px;
  color: #555;
  font-weight: bold;
  margin-bottom: 10px;
}
.block-title::after{
  content: ':';
  display: inline-block;
  padding-left: 5px;
}
.exp-content-container{
  margin-bottom: 20px;
  line-height: 20px;
}
.description-list{}
.description-list-item{
  margin-bottom: 10px;
  line-height: 20px;
  list-style-type: disc;
  margin-left: 20px;
}

/* Skills */
.progressbar-container{
  margin-bottom: 20px;
}
.progressbar-container span{
  display: none;
}

/* Education */
.education-section-block {
  margin-bottom: 20px;
}

.education-section-block > div p {
  line-height: 20px;
  margin-bottom: 10px;
}